body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #e4d7fd;
  min-height: 100vh;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}
#home,
#list,
#detail,
#content {
  padding-bottom: 40px;
  overflow-x: hidden;
  max-width: 1200px;
  margin: auto;
}
#mainbox {
  padding-bottom: 120px;
}
#newlist {
  max-width: 900px;
  margin: auto;
  padding-bottom: 120px;
}
#footers {
  position: fixed;
  left: 0;
  bottom: 0;
  background-color: rgba(227, 164, 242, 0.8);
  line-height: 100px;
  font-size: 12px;
  width: 100%;
  display: block;
  text-align: center;
}
#footers .btn {
  width: 210px;
  display: inline-block;
  line-height: 40px;
  text-align: center;
  font-size: 12px;
  background-color: #2d1856;
  color: #fff;
  padding: 0 10px;
  border-radius: 20px;
  margin: 20px 40px;
  border: 1px solid #333;
  cursor: pointer;
}
.slick-slider img {
  display: block;
  width: 100%;
}
.adsbygoogle {
  display: none;
}
header {
  overflow: hidden;
  background-color: #2d1856;
}
header .topnav {
  float: right;
  display: flex;
  margin: 30px 0;
}
header .topnav .btn {
  padding: 0 10px;
  border-radius: 20px;
  margin: 0 20px;
  width: 90px;
}
header .btn.active {
  background-color: #f4f4f4;
  color: #2d1856;
}
header .btn:hover {
  background-color: #f4f4f4;
  color: #2d1856;
}
header .btn {
  text-align: center;
  font-size: 14px;
  line-height: 40px;
  color: #fff;
}
header .btn svg {
  margin-right: 10px;
}
